// Necessary WP classes
.wp-caption {
    font-size: inherit;
}

.wp-caption-text {
    font-size: inherit;
}

.alignleft {
    display: inline;
    float: left;
    margin-right: 1.5em;
}

.alignright {
    display: inline;
    float: right;
    margin-left: 1.5em;
}

.aligncenter {
    @extend .mx-auto,
    .d-block;
}

// Post design
.entry-footer span {
    padding-right: 10px;
}

img.wp-post-image,
article img,
figure,
img,
#secondary img {
    max-width: 100%;
    height: auto;
}

// Skip to content link
a.skip-link {
    position: fixed;
    z-index: 1000;
    top: 0px;
    right: 0px;
}

// Galleries
.gallery {
    margin-bottom: 1.5em;
}
.gallery-item {
    display: inline-block;
    text-align: center;
    vertical-align: top;
    width: 100%;

    @for $i from 2 through 9 {
	.gallery-columns-#{$i} & {
	    max-width: floor( percentage( 1 / $i ) * 100 ) / 100;
	}
    }
}
.gallery-caption {
    display: block;
}
