/*Site-header*/
.site-header {
  .navbar-dark {
    background: $dark-blue;
  }
  .logo-link {
    width: rem(174px);
  }
  .nav-link {
    text-transform: uppercase;
    border-width: 4px 0;
    border-style: solid;
    border-color: transparent;
  }
  .active .nav-link {
    border-bottom-color: $primary;
    font-weight: 900;
  }
  .dropdown:hover {
    .dropdown-menu {
      display: block;
    }
  }
}
.navbar-toggler {
  border: 0 none;
  padding-right: 0;
  position: relative;
  z-index: 200;
  margin: .5rem 0 1rem;
  transition: all .3s ease;
  .btn-text {
    font-size: .8rem;
    line-height: 20px;
    color: $primary;
    font-weight: 700;
    text-transform: uppercase;
  }
  .toggle-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    position: relative;
    top: 4px;
    margin-right: 5px;
    i {
      display: block;
      position: absolute;
      height: 3px;
      width: 100%;
      background-color: $primary;
      left: 0;
      transform: rotate(0deg);
      transition: .25s ease-in-out;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2),
      &:nth-child(3) {
        top: 8px;
      }
      &:nth-child(4) {
        top: 16px;
      }
    }
  }
  &:not(.collapsed) {
    .toggle-icon {
      i {
        &:nth-child(1) {
          top: 9px;
          width: 0;
          left: 50%;
        }
        &:nth-child(2) {
          transform: rotate(45deg);
        }
        &:nth-child(3) {
          transform: rotate(-45deg);
        }
        &:nth-child(4) {
          top: 9px;
          width: 0;
          left: 50%;
        }
      }
    }
  }
}
.site-footer {
  padding: 3rem 0;
  background-color: $gray-900;
  color: #fff;
  .row {
    justify-content: space-between;
  }
  .footer-logo {
    display: inline-block;
    margin-bottom: 2rem;
    img {
      width: rem(240px);
    }
  }
  .widget-title {
    color: #fff;
    margin-bottom: rem(30px);
  }
  ul {
    list-style: none;
    padding-left: 0;
    font-size: rem(18px);
    max-width: rem(243px);
    li + li {
      margin-top: 1rem;
    }
  }
  .post-date {
    display: block;
    color: $gray-600;
  }
  .contact-us {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .btn {
      width: 12rem;
    }
  }
  .contacts {
    font-size: rem(16px);
    a {
      color: #fff;
      &:hover {
        color: $primary;
      }
    }
  }
}
.copyright {
  padding: rem(22px 0);
  background-color: $gray-800;
  color: $gray-600;
  font-size: 13px;
}
