@include media-breakpoint-up(sm) {
  .site-footer {
    ul {
      font-size: rem(14px);
      li + li {
        margin-top: 2rem;
      }
    }
  }
}
@include media-breakpoint-up(md) {
  .site-header {
    .navbar-collapse {
      justify-content: flex-end;
    }
    .main > .nav-item + .nav-item {
      margin-left: rem(30px);
    }
  }
}
@include media-breakpoint-up(lg) {
  .site-header {
    .close-mobile-menu {
      display: none;
    }
  }
}
@include media-breakpoint-only(lg) {
}
@include media-breakpoint-up(xl) {
  .site-footer {
    .contact-us {
      .btn {
        width: rem(296px);
      }
    }
  }
}
@include media-breakpoint-down(md) {
  .site-header {
    position: relative;
    .navbar {
      padding-top: .5rem;
      padding-bottom: .5rem;
    }
    .logo-link {
      width: 8rem;
    }
    .navbar-collapse {
      position: fixed;
      z-index: 1000;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      padding: 90px 30px 30px;
      background: rgba($dark-blue, .95);
      min-height: 100vh;
      height: 100%;
      width: 100vw;
      overflow-y: auto;
      .dropdown-menu {
        display: block;
        background: none;
      }
      .dropdown-item {
        white-space: normal;
      }
      .nav-link {
        font-size: 18px;
      }
      .dropdown-item {
        font-size: 18px;
      }
    }
    .navbar-toggler:not(.collapsed) {
      position: absolute;
      z-index: 2000;
      top: 1rem;
      right: 1rem;
    }
  }
}
@include media-breakpoint-down(sm) {
  .site-footer {
    text-align: center;
    ul {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
    .contacts li {
      display: inline-block;
    }
    .order-lg-1 ul {
      max-width: 100%;
    }
    .col-sm-6 + .col-sm-6 {
      margin-top: 2rem;
    }
  }
  .copyright {
    text-align: center;
  }
}