* {
  outline: none !important;
}
a {
  transition: all .3s ease;
}
html {
  font-size: 15px;
  scroll-behavior: smooth;
}
body {
  min-width: 320px;
  position: relative;
  image-rendering: -moz-crisp-edges;
}
b, strong {
  font-family: $headings-font-family;
}
.fsz-18 {
  font-size: rem(18px);
}
svg.icon {
  width: 1em;
  height: 1em;
}
.invalid {
  .form-control:invalid {
    border-color: $primary;
  }
}
.fw-black {
  font-weight: 900;
}
.light-bg {
  background-color: $gray-100;
}
.bg-dark {
  background-color: $dark-blue;
  color: #fff;
}
.btn-primary {
  background-color: #0f1c3b;
  background-image: linear-gradient(89deg, #38ae0a 0%, #52d20b 100%);
  &:hover {
    background-image: linear-gradient(89deg, #52d20b 0%, #38ae0a 100%);
  }
}
.text-block {
  .read-more {
    margin-top: -1rem;
  }
}
.content-section {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  .row {
    justify-content: center;
  }
}
.page-header {
  background-color: #0f4b89;
  .h1 {
    font-weight: 900;
    color: #fff;
    text-transform: uppercase;
    line-height: 1.1;
    position: relative;
    padding-left: 1rem;
    margin-bottom: 0;
    &:before {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      bottom: 0;
      width: 4px;
      background-color: $primary;
    }
  }
}
.contacts {
  list-style: none;
  padding-left: 0;
  font-size: rem(18px);
  a {
    color: $body-color;
    &:hover {
      color: $primary;
    }
  }
  li {
    padding-left: rem(30px);
    background-position: rem(3px);
    background-repeat: no-repeat;
    background-size: rem(15px);
  }
  li + li {
    margin-top: 1rem;
  }
