}
.fw-heading-h2.text-center {
  .fw-special-title {
    position: relative;
    padding-bottom: 18px;
    &:after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 45px;
      height: 4px;
      background: $primary;
    }
  }
}
.faq {
  .accordion-item {
    border-bottom: 1px solid $gray-300;
    font-size: rem(17px);
  }
  .accordion-button {
    @include font-size(rem(20px));
    font-weight: 700;
    padding-left: rem(70px);
    &:after {
      position: absolute;
      left: 0;
    }
  }
  .accordion-body {
    padding-left: rem(70px);
  }
}
.main-content {
  .card {
    max-width: 800px;
  }
}
.widget + .widget {
  margin-top: 2rem;
}
.card {
  + .card {
    margin-top: 2rem;
  }
  .card-title {
    a {
      color: $gray-900;
    }
  }
  .btn-primary {
    border-radius: 2rem;
  }
  .entry-meta {
    margin: 1.9rem 0;
  }
  .excerpt {
    margin-bottom: rem(30px);
  }
}
.entry-meta {
  color: #999999;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  li {
    line-height: rem(20px);
    padding-left: rem(30px);
    background-position: 0 50%;
    background-size: rem(20px);
    background-repeat: no-repeat;
    + li {
      margin-left: 25px;
    }
  }
  .date {
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg' fill='%23999999'%3E%3Cpath class='cls-1' d='M16,31A15,15,0,1,1,31,16,15,15,0,0,1,16,31ZM16,3A13,13,0,1,0,29,16,13,13,0,0,0,16,3Z'/%3E%3Cpath d='M20.24,21.66l-4.95-4.95A1,1,0,0,1,15,16V8h2v7.59l4.66,4.65Z'/%3E%3C/svg%3E");
