.sidebar {
  .widget-title {
    margin-bottom: 2rem;
  }
  .post {
    + .post {
      margin-top: rem(20px);
    }
    .row {
      align-items: center;
    }
    .col-4 {
      width: 110px;
    }
    .col-8 {
      width: calc(100% - 110px);
    }
    h3 {
      font-size: 1rem;
      margin-bottom: .5rem;
      a {
        color: #0f4b89;
      }
    }
    .entry-meta li + li {
      margin-left: 10px;
    }
  }
}
.pagination {
  li + li {
    margin-left: rem(11px);
  }
  li {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.16);
    &.active {
      box-shadow: none;
    }
  }
}
.single {
  .entry-content {
    padding: rem(30px);
    background-color: #fff;
  }
  .content {
    ul {
      list-style: none;
      padding-left: 1rem;
      font-size: 14px;
      li {
        margin-bottom: 1rem;
      }
      li::before {
        content: "\2022";
        color: $primary;
        font-weight: bold;
        display: inline-block;
        width: 1em;
        margin-left: -1em;
      }
    }
  }
}
#main-banner {
  h1 {
    line-height: 1.1;
    position: relative;
    padding-left: 1rem;
    margin-bottom: 0;
    color: #05498c;
    &:before {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      bottom: 0;
      width: 4px;
      background-color: $primary;
    }
  }
}
#second-banner {
  .fw-heading {
    background: url(../img/second-banner.jpg) 50% / cover no-repeat;
    min-height: 20rem;
    padding: rem(30px 15px);
    display: flex;
    align-items: center;
    .fw-special-title {
      color: #fff;
      font-weight: 300;
      max-width: 445px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        bottom: -30px;
        left: 0;
        display: block;
        width: 64px;
        height: 4px;
        background-color: $primary;
      }
    }
  }
}
.latest-news {
  background: url(../img/latest-news.jpg) 50% / cover no-repeat;
  .fw-special-title {
    color: #fff;
  }
  .owl-stage {
    display: flex;
  }
}
.post-owl {
  h4 {
    margin-bottom: 0;
    a {
      color: #232323;
      font-size: rem(20px);
    }
  }
  .date {
    text-transform: uppercase;
    margin-bottom: .3rem;
  }
}